@keyframes flicker {
  0% {
    opacity: 1;
    transform: scale(1);
    stroke: #D84727;
    /* Main color */
  }

  50% {
    opacity: 0.5;
    transform: scale(1.10);
    stroke: #F75946;
    /* Lighter shade */
  }

  100% {
    opacity: 1;
    transform: scale(1);
    stroke: #A0361D;
    /* Darker shade */
  }
}

.flame-icon svg path {
  animation: flicker 1s infinite alternate;
}

@keyframes rotate-hashtag {
  0%, 100% {
    transform: rotate(0deg);
  }

  50% {
    transform: rotate(20deg);
  }
}

.hashtag {
  display: inline-block;
  animation: rotate-hashtag 1s infinite ease-in-out;
}

